/* Share Bar */
#share-bar {
    font-size: 21px;
}

.share-buttons {
    margin-bottom: $spacing-unit / 2;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    max-width: 370px;
    grid-gap: $spacing-unit / 4;
}

.share-button {
    display: flex;
    flex-direction: column;
}

.share-button > a > i {
    color: white;
    border: 1px solid $grey-color-light;
    display: block;
    text-align: center;
    padding: $spacing-unit/6;
}

.share-button > a > i:hover {
    opacity: 1;
    background-color: white;
}

.fa-facebook-official {
    background-color: #3b5998;
}

.fa-facebook-official:hover {
    color: #3b5998;
}

.fa-whatsapp {
    background-color: #34af23;
}

.fa-whatsapp:hover {
    color: #34af23;
}

.fa-twitter {
    background-color: #55acee;
}

.fa-twitter:hover {
    color: #55acee;
}

.fa-tumblr {
    background-color: #32506d;
}

.fa-tumblr:hover {
    color: #32506d;
}

.fa-linkedin {
    background-color: #007bb5;
}

.fa-linkedin:hover {
    color: #007bb5;
}

.fa-envelope {
    background-color: #444444;
}

.fa-envelope:hover {
    color: #444444;
}
