/**
 * Posts
 */
.post-title {

  margin-bottom: $spacing-unit;

  @include relative-font-size(2.25);
  letter-spacing: -1px;
  line-height: 1;

  @include media-width-above($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.post-content {
  margin-bottom: $spacing-unit;
  flex: 1;

  .reading-time {
    font-size: $very-small-font-size;
    color: $grey-color;
  }

  .img-container-wrapper {
      max-width: $content-width;
      display: grid;
      grid-gap: $spacing-unit;

      grid-template-columns: none; /* eine Spalte */

      @include media-width-above($on-laptop) {
        grid-template-columns: 1fr 1fr; /* zwei Spalten */
      }

      img {
          padding-bottom: $spacing-unit / 2;
      }
  }

  .intro-text {
      display: flex;
      text-align: justify;
  }

  .lineup-image {
      padding-bottom: $spacing-unit / 2;
  }

  h2 {
    @include relative-font-size(1.75);

    @include media-width-above($on-laptop) {
      @include relative-font-size(1.75);
    }

    padding-top: $spacing-unit / 2;
  }

  h3 {
    @include relative-font-size(1.25);

    @include media-width-above($on-laptop) {
      @include relative-font-size(1.125);
    }

    font-weight: normal;
    padding-top: $spacing-unit / 2;
  }

  h4 {
    @include relative-font-size(1.125);

    @include media-width-above($on-laptop) {
      @include relative-font-size(1.1);
    }
  }

  h3 a.internal-link {
      border-bottom: 3px solid $brand-color-contrary;
  }

  a.internal-link {
      border-bottom: 1px solid $brand-color-contrary;
  }

  .video-wrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
  }

  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

hr.divider {
  border: 0;
  height: 1px;
  background-image: -webkit-linear-gradient(left, #f0f0f0, #e8e8e8, #f0f0f0);
  background-image: -moz-linear-gradient(left, #f0f0f0, #e8e8e8, #f0f0f0);
  background-image: -ms-linear-gradient(left, #f0f0f0, #e8e8e8, #f0f0f0);
  background-image: -o-linear-gradient(left, #f0f0f0, #e8e8e8, #f0f0f0);
  margin-bottom: $spacing-unit/2;
}

.share-this-slogan {
  font-weight: bold;
}

.post-meta {
    padding: $spacing-unit/2 0 $spacing-unit/2 $spacing-unit/2;
    font-size: $small-font-size;
    color: $grey-color;
    background-color: $grey-color-light;
    margin-bottom: $spacing-unit/2
}

.vgwort-container {
    height: 1px;
}
