.related-posts {
    display: grid;
    max-width: $content-width;
    margin-bottom: $spacing-unit;

    grid-template-columns: repeat(2,1fr);
    grid-gap: $spacing-unit / 4;

    @include media-width-above($on-laptop) {
        grid-template-columns: repeat(3,1fr);
        grid-gap: $spacing-unit / 2;
    }

    .related-post {
        display: flex;
        flex-direction: column;

        h3 {
            padding-top: 0;
            margin-bottom: 0;
            @include relative-font-size(1.125);
        }

        .related-img-container-wrapper {
          height: 180px;
          overflow: hidden;
        }

        .related-img-container-wrapper:hover, .related-img-container-wrapper:focus {
            .img-container{
                transform: scale(1.1);
            }
        }

        .img-container {
          background-color: black;
          height: 100%;
          width: 100%;
          background-size: cover;
          background-repeat: no-repeat;
          -webkit-transition: all .4s;
          -moz-transition: all .4s;
          -o-transition: all .4s;
          transition: all .4s;
          -webkit-filter: grayscale(60%);
          filter: grayscale(60%);
        }
    }
}
