/**
 * Page content
 */
.page-content {
  padding-bottom: 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;
  display: grid;
  max-width: $content-width;
  grid-gap: $spacing-unit / 2;

  @include media-width-below($on-palm) {
    grid-template-columns: none;
  }

  @include media-width-above($on-palm) {
    grid-template-columns: none;
  }

  @include media-width-above($on-laptop) {
    grid-template-columns: 1fr 1fr; /* zwei Spalten */ /*grid-template-columns: repeat(2,1fr);*/
  }

  @include media-width-above($on-desktop) {
    grid-template-columns: none;
  }

  @include media-width-above($on-wide-screen) {
    grid-template-columns: 1fr 1fr; /* zwei Spalten */
  }

  li {
    display: flex;
    flex-direction: column;
    max-width: $max-img-width;
    margin-bottom: $spacing-unit;
  }

  .img-container-wrapper {
    height: $max-img-height;
    max-width: $max-img-width;
    overflow: hidden;
    margin-bottom: $spacing-unit / 2;
  }

  .img-container-wrapper:hover .img-container, .img-container-wrapper:focus .img-container {
    transform: scale(1.1);
  }

  .img-container {
    background-color: black;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-transition: all .4s;
    -moz-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    -webkit-filter: grayscale(60%);
    filter: grayscale(60%);
  }
  img {
      max-height: 250px;
  }
  .post-list-excerpt {
      text-align: justify;
      flex-grow: 1;
  }
  .button {
      float: right;
      border: 1px solid $brand-color;
      color: white;
      background-color: $brand-color;
      padding: $spacing-unit / 4;
  }
  .button:hover {
      color: $brand-color;
      background-color: white;
      transition: all .1s;
  }
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}

.pagination-divider {
    margin-bottom: $spacing-unit;
    border-bottom: 1px solid $grey-color-light;
}

.pagination {
    text-align: center;

    .button-container {
        display: inline-block;
    }
    .button {
        border: 1px solid $brand-color;
        color: white;
        background-color: $brand-color;
        padding: $spacing-unit / 4;
    }
    .button:hover {
        color: $brand-color;
        background-color: white;
        transition: all .1s;
    }
    .button-inactive {
        background-color: white;
        color: $brand-color;
        cursor: default;
    }
}
