.search-input {
    -webkit-appearance: none;
    border: 0;
    border-radius: .2em;
    font-family: inherit;
    font-size: 1.25em;
    padding: 0.5em;
    width: 100%;
    border: 1px solid $grey-color-light;
    margin-bottom: $spacing-unit;
}

.search-result-message {

}


.searchhit {
    margin-bottom: $spacing-unit;
}

.searchhit-title {
    @include relative-font-size(1.375);
}

.searchhit-excerpt {

}

.searchhit-meta {

}

.searchhit-meta-team {
    margin-right: $spacing-unit / 4;
    border: 1px solid $grey-color-light;
    padding: 2px;
    transition: all 0.1s;
}

.searchhit-meta-team:hover {
    color: white;
    background-color: $brand-color;
}
