
.tags-item-container {

    border-bottom: 1px solid $grey-color-light;
    padding-top: $spacing-unit/4;
    padding-bottom: $spacing-unit/4;
}

.tags-item {
    display: inline-block;
    margin: 1px;
    padding: 1px;
}

.tags-item a:hover {
    text-decoration: none;

    span {
        color: $brand-color;
    }
}

.tag-list-item-img {
    width: 35px;
    padding-right: 5px;
}

.tags-item-sub-container {
    padding-left: 46px;

    @include media-width-above($on-laptop) {
      column-count: 2;
    }

    .internal-link {
      color: $brand-color;
      border-bottom: 1px solid $brand-color-contrary;
    }
}

.tags-item-name, .tags-sub-item-name {
    white-space: nowrap;
    color: $text-color;
}

.tags-sub-item-name:hover {
    color: $brand-color;
}
