/**
 * Site header
 */
.site-header {
  border-top: none !important;
  border-bottom: 1px solid $grey-color-light;
  min-height: $spacing-unit * 1.865;
  padding-left: $spacing-unit / 2;
  padding-right: $spacing-unit / 2;

  @include media-width-above($on-wide-screen) {
    margin-left: auto;
    margin-right: auto;
  }

  .logo {
    display: block;
    float: left;
    width: 150px;
    max-width: 300px;

    @include media-width-below($on-palm) {
      width: 100px;
    }

    @include media-width-above($on-wide-screen) {
      width: 200px;
    }

  }

  @keyframes gradient {
  	0% {
  		background-position: 0% 50%;
  	}
  	50% {
  		background-position: 100% 50%;
  	}
  	100% {
  		background-position: 0% 50%;
  	}
  }

  h1 {
    float: left;
    font-weight: bold;
    text-transform: uppercase;
    font-family: 'Titillium Web', sans-serif;
    filter: grayscale(25%);
    -webkit-filter: grayscale(25%);

    /* das funktioniert nur in webkit Browsers */
    color: white;  /* Fallback Farbe */
    /*background: url(images/fussballrasen.jpg) no-repeat;*/

    background: linear-gradient(to right, #3f8465, #E48624, #ead0b6);
/*
    background-size: 400% 400%;
	  animation: gradient 15s ease infinite;
*/
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    @include media-width-below($on-palm) {
      padding-top: 42px;
    }

    @include media-width-above($on-palm) {
      padding-top: 27px;
      margin-bottom: 0;
    }

    @include media-width-above($on-wide-screen) {
      padding-top: 39px;
    }

    .first-word {
      display: block;

      @include media-width-below($on-palm) {
        font-size: 45px;
        line-height: 35px;
      }

      @include media-width-above($on-palm) {
        font-size: 83px;
        line-height: 70px;
      }

      @include media-width-above($on-wide-screen) {
        font-size: 115px;
        line-height: 95px;
      }
    }

    .second-word {
      display: block;

      @include media-width-below($on-palm) {
        font-size: 45px;
        line-height: 35px;
      }

      @include media-width-above($on-palm) {
        font-size: 83px;
        line-height: 70px;
      }

      @include media-width-above($on-wide-screen) {
        font-size: 115px;
        line-height: 88px;
      }
    }
  }


  /**
   * Responsive navigation
  */
  .burger-icon {
    display: none;
  }

  .burger-word {
    margin: 0;
    padding: 0;
    font-size: 10px;
    margin-top: -5px;
    text-transform: uppercase;
  }

  .topnav {
    clear: left;
    overflow: hidden;
    position: relative;
    z-index: 1;
  }

  .topnav a {
    float: left;
    display: block;
    text-transform: uppercase;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 300;

    @include media-width-below($on-palm) {
      /* Anzeige auf kleinem Display unterdrücken */
      display: none;
    }

    color: $text-color;
    padding: $spacing-unit/2 $spacing-unit;
    text-align: center;
    text-decoration: none;
    font-size: $base-font-size;
  }

  .topnav a:hover {
    background-color: $brand-color-contrary;
    color: white;
  }

  .topnav a.active {
    color: $brand-color;
    font-weight: bold;
  }

  @include media-width-below($on-palm) {

    .burger-icon {
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      display: block;
      color: $text-color;
      padding: 12px 24px;
      text-align: center;
      text-decoration: none;
      font-size: $base-font-size;
    }

    .burger-icon:hover {
      background-color: $brand-color-contrary;
      color: white;
    }

    .burger-icon:active {
      background-color: $brand-color;
      color: white;
    }

    .topnav.responsive {
      position: relative;
    }

    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
}
