/**
 * Sidebar
 */
.sidebar h3 {
    border-bottom: 1px solid lightgray;
}

.sidebar-item {
    padding-bottom: $spacing-unit;
}

.sidebar-item ul {
    margin: 0;
    margin-left: $spacing-unit / 4;

    li {
        list-style: none;
        border-bottom: 1px solid lightgray;

        a {
            display: block;
            padding: $spacing-unit / 4;
            padding-left: 0;
        }
    }

    li:first-child a {
        padding-top: 0;
    }

    li:last-child {
        border-bottom: none;
    }
}

.tag-list {
}

.tag-list-item {
    display: inline-block;
    margin: 2px;
    padding: 2px 4px;
    border: 1px solid #3e8465;

    a {
        display: inline-block;
    }
}

.tag-list-item:hover {
    background-color: $brand-color;
    transition: all 0.1s;

    a {
        color: white;
    }
}
