@charset "utf-8";

// Define defaults for each variable.
$base-font-family: "Calibri", serif !default;
$base-font-family-header: Helvetica, Arial, sans-serif;
$on-wide-screen-font-size:   19px !default;
$base-font-size:   18px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$very-small-font-size: 12px;
$base-line-height: 1.5 !default;

$spacing-unit:     30px !default;

$text-color:           #383431 !default;
$background-color:     #fdfdfd !default;
$brand-color:          #3e8465 !default;
$brand-color-contrary: #e48625 !default;
$brand-color-contrary-light: #ead0b6 !default;

$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

$table-text-align: left !default;

// Width of the content area
$content-width:    1200px !default;
$sidebar-width:    320px !default;

$on-palm:        600px !default;
$on-laptop:      768px !default;
$on-desktop:     992px !default;
$on-wide-screen: 1200px !default;

$max-img-height: 350px !default;
$max-img-width: 450px !default;

// Use media queries like this:
// @include media-width-above($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }

@mixin media-width-above($device) {
  @media screen and (min-width: $device) {
    @content;
  }
}

@mixin media-width-below($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// do not use this:
@mixin media-query($device) {
  @media screen and (min-width: $device) {
    @content;
  }
}

// Import partials.
@import
  "minima/about",
  "minima/base",
  "minima/footer",
  "minima/header",
  "minima/layout",
  "minima/lesetipps",
  "minima/page",
  "minima/post",
  "minima/related-posts",
  "minima/search",
  "minima/sharebuttons",
  "minima/sidebar",
  "minima/syntax-highlighting",
  "minima/vereine"
;
